#mainYear{
    background-color: #9F99AC;
    color: white;
    margin-left: 15%;
    margin-right: 15%;
    font-size: xx-large;
    border-radius: 1%;
    padding: 3px;
    border: solid 2px #7F7494;
    transition: 1.2s;
    visibility: hidden;
}

#secondaryYear{
    background-color: rgb(95, 95, 95);
    margin-top: 5px;
    color: white;
    margin-left: 20%;
    margin-right: 20%;
    font-size: xx-large;
    border-radius: 1%;
    padding: 3px;
    border: solid 2px #9F99AC;
    transition: 1.2s; 
}

#secondaryYear:nth-of-type(2){
   visibility: hidden;

}
#secondaryYear:nth-last-of-type(2) {
 margin-bottom: 3em;
 /*margin on bottom*/
}
#yearsChecked{
  margin-left: 1%;
}

@keyframes slideben {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

#bbico{
  width: 8%;
  animation: 1s ease-out 0s 1 slideben;
}

.slideDown {
    transition: 1.2s;
    margin-top: 75px;
}
.menu{
    margin: 0;
    padding-top: 2em;
    padding-bottom: 4em;
    background-image: url("bg3.jpg");
    background-position-y: 35%;
    background-position-x: 4%;
    background-size: 147%;
    border-bottom: 5px solid #af8d6e;

}
#bigbimg{
  padding-top: -3em;
}
#title{
    font-size: xx-large;
    color: #b88840;
    font-weight: 900;
    padding-bottom: 1em;
    animation: 1s ease-out 0s 1 slideInFromLeft;
}
input{
    border: solid 2px #91653d;
    height: 1.5em;
    animation: 1s ease-out 0s 1 slideInFromRight;
    width: 14%;
    font-size: larger;
    margin-top: -1.3em;

}
input:hover{
    cursor: pointer;
}

  @keyframes slideInFromLeft {
    0% {
      transform: translateX(-100%);
    }
    100% {
      transform: translateX(0);
    }
  }
  @keyframes slideInFromRight {
    0% {
      transform: translateX(+100%);
    }
    100% {
      transform: translateX(0);
    }
  }
  

  
html, body{
    margin-left: 0;
    margin-right: 0;
    margin-top: 0;
    background-color:#b1a6a1;
}
.footer{
    margin-top: 10px;
    background-color: #bebbc2;
    bottom: 0;
    position: absolute;
    width: 100%;
    z-index: -1;
}
h1{
    background-color: wheat;
}